<template>
<div id="components-layout-demo-basic">
  <a-layout>
    <a-layout-header>
      <img src="data:image/png;base64,"/>
      <span>全民 Party 豬頭王 🐷 </span>
      </a-layout-header>
    <a-layout-content>
      
      <br />

      <a-row v-if="admin" type="flex" justify="space-around" align="middle">
        <a-col :span="12">
          <span>時間筆數：{{ times_length }}</span>
          <a-textarea v-model="times" placeholder="請貼上房間時間" auto-size />
        </a-col>
        <a-col :span="12">
          <span>房間筆數： {{ rooms_length }}</span>
          <a-textarea v-model="rooms" placeholder="請貼上房間號碼" auto-size />
        </a-col>
        
      </a-row>
      
      <a-button @click="save_rooms" v-if="rooms" type="primary" block>
        產生
      </a-button>

      <a-textarea v-if="admin" v-model="link" auto-size />
      <!-- <a-button @click="recover_room" type="dashed" style="color: red" block>復原房號 {{this.last_room}} </a-button>
      <br />
      <br /> -->
    <!-- <p v-if="this.party_nos.length > 0">還剩下幾隻豬仔🐷：{{this.party_nos.length}}</p> -->
    <div v-if="this.party_nos.length > 0" style="background: #ececec">
      <a-row>
        <a-col :span="12">
          <a-card>
            <a-statistic title="剩下豬仔🐷" :value="this.party_nos.length" style="margin-right: 50px" />
          </a-card>
        </a-col>
        <a-col :span="12">
          <a-card>
            <a-statistic title="抓幾隻🐷" :value="this.pickup" style="margin-right: 50px" />
          </a-card>
        </a-col>
      </a-row>
      
      

    </div>
    <a-list style="margin-top: 10px; background: white;"  bordered :data-source="party_nos">
      <a-list-item slot="renderItem" slot-scope="item, index">
        <a slot="actions"><a-button @click="handle_link(index, item.room)">🐷 GO</a-button></a>
        <a slot="actions"><a-button @click="handle_close(index)">❌</a-button></a>
        <a-list-item-meta :description="item.time">
        <a slot="title">{{ item.room }} </a>
        
        <a-avatar slot="avatar">🚪 </a-avatar>
        </a-list-item-meta>
        
      </a-list-item>
    </a-list>
    <a-button style="margin-top: 10px;" @click="clear_rooms" v-if="party_nos.length > 0" type="danger" block>
        全部清除
      </a-button>
    </a-layout-content>
    <a-layout-footer>Copyright © 2021 全民Party 音浪空間 ᵞᴸ阿里柏</a-layout-footer>
  </a-layout>  
</div>

</template>

<script>



export default {
  data() {
    return {
      "party_icon": 'data:image/png;base64,',
      "rooms": '',
      "admin": false,
      "rooms_array": [],
      "times": '',
      "times_array": [],
      "party_nos": [],
      "todap_ping": 0,
      "link": "",
      "pickup": 0,

      // "last_room": 100188,
    }
  },
  methods: {
    save_rooms(){

      this.rooms_array = this.rooms.split("\n").filter(Number)
      this.times_array = this.times.split("\n").filter(word => word.length > 0)

      // if ( this.times_array.length != this.rooms_array.length ) {
      //   alert("兩邊數量不一致")
      //   return
      // }

      for (let i = 0; i < this.rooms_array.length; i++) {
        this.party_nos.push(
          {
            "time": this.times_array[i],
            "room": this.rooms_array[i],
          }
        )
      }
      

      let str_party = JSON.stringify(this.party_nos)
      console.log(str_party)
      this.link = window.location.href + '?b=' + window.btoa(JSON.stringify(this.party_nos))

      localStorage.setItem('party_nos', JSON.stringify(this.party_nos))

    },
    clear_rooms(){
      this.times = ""
      this.rooms = ""
      this.party_nos = []
      this.link = ""
      localStorage.removeItem('party_nos')
    },
    handle_link(index, no){
      this.party_nos.splice(index, 1);
      localStorage.setItem('party_nos', JSON.stringify(this.party_nos))
      this.pickup = this.pickup + 1;
      localStorage.setItem('pickup', this.pickup)
      window.open("https://party.haochang.tv/share/rooms?inviteCode=&roomCode="+ no +"&language=zh-TW&roomId=92190&channel=line&userId=23368648","subwin")
      // window.location.href = "https://party.haochang.tv/share/rooms?inviteCode=&roomCode="+ no +"&language=zh-TW&roomId=92190&channel=line&userId=23368648";
    },
    handle_close(index){
      this.party_nos.splice(index, 1);
      localStorage.setItem('party_nos', JSON.stringify(this.party_nos))
      this.link = window.location.origin + '/?b=' + window.btoa(JSON.stringify(this.party_nos))
    }
  },


  mounted() {

    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);

    if(urlParams.get('admin') == 1) {
      this.admin = true
    }

    if (urlParams.has('b')) {
      let b64 = urlParams.get('b')
      let tmp_nos = window.atob(b64)
      localStorage.setItem('party_nos', tmp_nos)
      localStorage.setItem('pickup', 0)
    }
    
    if (localStorage.party_nos) {
      this.link = window.location.origin + '/?b=' + window.btoa(localStorage.party_nos)
      this.party_nos = JSON.parse(localStorage.party_nos)
      this.pickup = localStorage.pickup

    }

    window.history.pushState("", "", '/');

  },
  computed:{

    times_length(){
      return this.times.split("\n").filter(word => word.length > 0).length
    },

    rooms_length(){
      return this.rooms.split("\n").filter(Number).length
    },

  }
}
</script>

